
import classes from './animeList.module.css'
import { useDispatch, useSelector } from 'react-redux'
import React from 'react'
import { actions } from '../../../store/store'
import { useNavigate } from 'react-router'
import AnimeSearch from './../animeSearch/animeSearch'


let AnimeList = function(){

    let apicall = async function(id) {
        await fetch(`https://kitsu.io/api/edge/anime/${id}`)
    .then(res => res.json()).then(result => dispatch({type:actions.setCurrAnime, 
        canonicalTitle: result.data.attributes.canonicalTitle,
        description: result.data.attributes.description,
        title_en: result.data.attributes.titles.en_jp,
        title_jp: result.data.attributes.titles.ja_jp,
        startDate: result.data.attributes.startDate,
        endDate: result.data.attributes.endDate,
        popularityRank: result.data.attributes.popularityRank,
        ageRating: result.data.attributes.ageRating,
        showType: result.data.attributes.showType,
        status: result.data.attributes.status,
        posterImage: result.data.attributes.posterImage.large,
        episodeCount: result.data.attributes.episodeCount,
        totalLength:  result.data.attributes.totalLength,
        coverImage: result.data.attributes.coverImage.small,
        youtubeVideoId: result.data.attributes.youtubeVideoId
    
    }))}

    let isFetching = useSelector(state => state.anime.isFetching)
    let navigate = useNavigate()
    let dispatch = useDispatch()
    let items = useSelector(state => state.anime.items)


    let onclickHandler = (e) =>{
        apicall(e.target.value)
        navigate('/anime/view')
    }
    
    return(
        <>  

            <AnimeSearch />
            <div className={classes.items}>

                {(items.length !== 0 && isFetching === false)
                ?
                    items.map(item => <div className={classes.item}>
                            <h1 className={classes.title}>{item.attributes.canonicalTitle}</h1>
                            <img src={item.attributes.posterImage.large} className={classes.poster}/>
                            <button className={classes.button} onClick={ onclickHandler } value={item.id}>More</button>
                            <h2 className={classes.startDate}>Started:      <span className={classes.attrs}>{item.attributes.startDate}</span></h2>
                            <h2 className={classes.episodeCount}>Episodes:  <span className={classes.attrs}>{item.attributes.episodeCount || 'ongoing'}</span></h2>
                            <h2 className={classes.showType}>Type:          <span className={classes.attrs}>{item.attributes.showType}</span></h2>
                            <h2 className={classes.popularity}>Rank:        <span className={classes.attrs}>{item.attributes.popularityRank}</span></h2>
                            <h2 className={classes.rating}>Age rating:      <span className={classes.attrs}>{item.attributes.ageRating}</span></h2>
                            <h2 className={classes.status}>Status:          <span className={classes.attrs}>{item.attributes.status}</span></h2>
                        
                                    </div>)
                    :
                        (isFetching === true)
                    ?
                        <div className={classes.fetching}>
                            <svg viewBox="0 0 100 100">
                                <defs>
                                    <filter className={classes.shadow}>
                                        <feDropShadow dx="0" dy="0" stdDeviation="1.5" 
                                        flood-color="#fc6767"/>
                                    </filter>
                                </defs>
                                <circle className={classes.spinner}  cx="50" cy="50" r="45"/>
                            </svg>
                        </div>
                    :
                        <div className={classes.error}>
                            <p>Nothing found or fill is empty</p>
                        </div>
                    
                }

                    
            </div>
        </>
    )
}

export default AnimeList