
import { combineReducers } from 'redux';
import { configureStore } from '@reduxjs/toolkit'
import { counterReducer } from './counterReducer';
import { Rule34Reducer } from './rule34Reducer';
import { notesReducer } from './notesReducer';
import { weatherReducer } from './weatherReducer';
import { animeReducer } from './animeReducer';
import { themeReducer } from './themeReducer';

export const actions = {
    searchRule34 : 'SEARCH_RULE34',
    increment : 'INCREMENT',
    decrement : 'DECREMENT',
    resetCounter: 'RESET_COUNTER',
    addNote: 'ADD_NOTE',
    deleteNote: 'DELETE_NOTE',
    searchWeather : 'SEARCH_WEATHER',
    resetRule34 : 'RESET_RULE34',
    weatherError: 'WEATHER_ERROR',
    setCurrAnime: 'SET_CURRENT_ANIME',
    resetAnime: 'RESET_ANIME',
    searchAnime: 'SEARCH_ANIME',
    setFetch: 'SET_FETCH_TRUE',
    switchWhite: 'SWITCH_WHITE_THEME',
    switchBlack: 'SWITCH_BLACK_THEME'
}

const rootReducer = combineReducers(
    {
        counter: counterReducer,
        rule34: Rule34Reducer,
        notes: notesReducer,
        weather: weatherReducer,
        anime: animeReducer,
        theme: themeReducer
    }
)

let store = configureStore( {reducer: rootReducer})

console.log(store.getState())
export default store