import React, { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { actions } from "../../store/store";
import classes from './notes.module.css'
import { v4 as uuidv4 } from 'uuid';
import Description from './../description/description'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';

import 'bootstrap/dist/css/bootstrap.min.css';

export default function Notes (props) {
    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')
    const [show, setShow] = useState(false);

    

    let dispatch = useDispatch()
    let notes= useSelector(state => state.notes.notes)
    // if (notes[0].keys(notes[0]).length === 0){
        
    // }

    const handleClose = () => {
        let id = uuidv4()
        dispatch({type:actions.addNote, payload: {id: id, title: title,body:body}})
        console.log(props)
        setShow(false)};

    const handleShow = () => setShow(true);

    const bodyhandler = (e) => {
        setBody(e.target.value)
    }

    const titlehandler = (e) => {
        setTitle(e.target.value)
    }

    return(
        <div>
            <Description description={'A simple note app with title and body'}/>
            <>
                <Button variant="secondary" onClick={handleShow} style={{'display':'block', 'margin':'0 auto'}}>
                    Add note
                </Button>

                <Modal show={show} onHide={() => setShow(false)}>
                    <Modal.Header closeButton>
                    <Modal.Title>Create note</Modal.Title>
                    </Modal.Header>
                    
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    
                                    <FloatingLabel controlId="floatingInputGrid" label="Note title">
                                    <Form.Control type="text" placeholder="" onChange={ titlehandler } maxLength={25}/>
                                    </FloatingLabel>
                                    
                
                                </Form.Group>

                                <FloatingLabel controlId="floatingTextarea2" label="Note body">
                                    <Form.Control
                                    as="textarea"
                                    maxLength={250}
                                    style={{'height':'100px'}}
                                    onChange={ bodyhandler }
                                    />
                                </FloatingLabel>
                                
                            </Form>
                        </Modal.Body>

                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShow(false)}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleClose}>
                            Add note
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>


            {/* <div className={classes.wrapper}>
                <input id={'title'} placeholder={'Enter title'} onChange={ titlehandler } className={classes.input}/>
                <textarea id={'body'} placeholder={'Enter your note'} onChange={ bodyhandler} className={classes.textarea}/>
                <button className={classes.button} onClick={ addhandler }>Add note</button>
            </div> */}
            
            <div className={classes.list}>
                {notes.map(note => 
                (
                    
                    <div className={classes.note}>
                        <h3 className={classes.title} >{note.title} </h3>
                        <h2 className={classes.body}>{note.body}</h2>
                    </div>
                ))}
            </div>


        </div>

    )
}