
import React from "react"

import classes from './weatherView.module.css'

import { useSelector } from "react-redux";
import moment from "moment";


export default function WeatherView (){

    let weather = useSelector(state => state.weather.weather)
    let city = useSelector(state => state.weather.city)
    let country = useSelector(state => state.weather.country)
    let isError = useSelector(state => state.weather.isWeatherError)
    let isFetching = useSelector(state => state.weather.isFetching)
    if (isError !== true){

        return(
            <div>
                
                <h1 className={classes.city}>{`${city} ${country}`}</h1>
                {(weather.length !== 0 && isFetching === false)
                ?
                    <div className={classes.forecast}>
                        {weather.map(item =>{
                            return(
                            <div className={classes.item}> 
                                <h3 className={classes.date}>{moment(item.valid_date).format('LL')}</h3>
                                <img className={classes.logo} src={`https://www.weatherbit.io/static/img/icons/${item.weather.icon}.png`} alt={`https://www.weatherbit.io/static/img/icons/${item.weather.icon}.png`}/>
                                <h3 className={classes.temp}>{`${item.temp}° (${item.min_temp}°-${item.max_temp}°)`}</h3>
                                <h4 className={classes.description}>{item.weather.description}</h4>
                                <h4 className={classes.wind}>Wind: {item.wind_spd} m/s || {item.wind_cdir_full}</h4>
                            </div>
                            )
                        })}
        
                    </div>
                :
                        (isFetching === true)
                    ?
                        <div className={classes.fetching}>
                            <svg viewBox="0 0 100 100">
                                <defs>
                                    <filter className={classes.shadow}>
                                        <feDropShadow dx="0" dy="0" stdDeviation="1.5" 
                                        flood-color="#fc6767"/>
                                    </filter>
                                </defs>
                                <circle className={classes.spinner}  cx="50" cy="50" r="45"/>
                            </svg>
                        </div>
                    :
                        <div className={classes.error}>
                            City not found
                        </div>
                    
                }
            </div>
        )
    }
    else{
            return(
                <div className={classes.error}>
                        City not found
                </div>
            )
        }
    }
    
