import { useState } from "react"
import React from "react"
import { useDispatch } from "react-redux"
import { actions } from "../../../store/store"
import { useNavigate } from "react-router"
import Description from "../../description/description"
import classes from './weatherSearch.module.css'

export default WeatherSearch => {

    const capitalize = s => s && s[0].toUpperCase() + s.slice(1)

    const dispatch = useDispatch()
    const navigate = useNavigate()
    let isError = false

    let apicall = async function(city,API_KEY) {
        dispatch({type: actions.setFetch})
        await fetch(`https://api.weatherbit.io/v2.0/forecast/daily?city=${capitalize(city)}&key=${API_KEY}`)
    .then(res => res.json())
    .then(result => 
    {
        dispatch({type:actions.searchWeather, weather: result.data, city: result.city_name, country: result.country_code})

    }).catch(()=>
    {
        navigate('/weather/error') 
        isError = true
    })
    }

    const [city, setCity] = useState('')
    

    const cityHandler = (e) => {
        setCity(e.target.value)
    }

    const buttonHandler = () => {
        apicall(city,'bce94d1059164045af43bf1fc95b44a4')
        debugger
        if (isError === false){
            navigate('/weather/view')
        }
    }
    
    
    return(
        <>
        <Description description='Simple forecast app for 3 days (free api limit)' />
        <div className={classes.wrapper}>
            
            <input className={classes.input} type='text' placeholder="Enter city" onChange={ cityHandler } value={city}/>
            <button className={classes.button} onClick={ buttonHandler }>Search</button>
        </div>
        </>
        
    )
}